.div-wrapper {
  height: 15px;
  width: 15px;
}

.div-wrapper .icons-4 {
  height: 15px;
}

.div-wrapper .overlap-group-3 {
  height: 15px;
  position: relative;
  width: 15px;
}

.div-wrapper .icons-5 {
  height: 15px;
  left: 0;
  position: absolute;
  top: 0;
  width: 15px;
}

.div-wrapper .rectangle-5 {
  border: 1px solid;
  border-color: var(--yellow);
  height: 10px;
  left: 0;
  position: absolute;
  top: 0;
  width: 10px;
}

.div-wrapper .rectangle-6 {
  border: 1px solid;
  border-color: var(--yellow);
  height: 10px;
  left: 5px;
  position: absolute;
  top: 5px;
  width: 10px;
}
