.button-hire-me {
  align-items: flex-start;
  display: inline-flex;
  flex-direction: column;
  position: relative;
}

.button-hire-me .button-hire-me-wrapper {
  align-items: flex-start;
  display: inline-flex;
  flex: 0 0 auto;
  flex-direction: column;
  position: relative;
}

.button-hire-me .group-wrapper {
  align-items: center;
  background-color: var(--yellow);
  border-radius: 5px;
  display: inline-flex;
  flex: 0 0 auto;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  padding: 16px 32px;
  position: relative;
}

.button-hire-me .group {
  height: 19px;
  margin-right: -2px;
  position: relative;
  width: 92px;
}

.button-hire-me .text-wrapper {
  color: var(--black);
  font-family: "Inter", Helvetica;
  font-size: 16px;
  font-weight: 500;
  height: 19px;
  left: 0;
  letter-spacing: 0;
  line-height: normal;
  position: absolute;
  text-align: center;
  top: 0;
  white-space: nowrap;
}

.button-hire-me .frame {
  height: 16px;
  left: 74px;
  position: absolute;
  top: 1px;
  width: 16px;
}
