.concrete-component-node {
  height: 15px;
  width: 15px;
}

.concrete-component-node .icons-6 {
  height: 15px;
}

.concrete-component-node .overlap-group-4 {
  height: 15px;
  position: relative;
  width: 15px;
}

.concrete-component-node .icons-7 {
  height: 15px;
  left: 0;
  position: absolute;
  top: 0;
  width: 15px;
}

.concrete-component-node .rectangle-7 {
  border: 1px solid;
  border-color: var(--yellow);
  height: 10px;
  left: 0;
  position: absolute;
  top: 0;
  width: 10px;
}

.concrete-component-node .rectangle-8 {
  border: 1px solid;
  border-color: var(--yellow);
  height: 10px;
  left: 5px;
  position: absolute;
  top: 5px;
  width: 10px;
}
