.icons-wrapper {
  height: 15px;
  width: 15px;
}

.icons-wrapper .icons-2 {
  height: 15px;
}

.icons-wrapper .overlap-group-2 {
  height: 15px;
  position: relative;
  width: 15px;
}

.icons-wrapper .icons-3 {
  height: 15px;
  left: 0;
  position: absolute;
  top: 0;
  width: 15px;
}

.icons-wrapper .rectangle-3 {
  border: 1px solid;
  border-color: var(--yellow);
  height: 10px;
  left: 0;
  position: absolute;
  top: 0;
  width: 10px;
}

.icons-wrapper .rectangle-4 {
  border: 1px solid;
  border-color: var(--yellow);
  height: 10px;
  left: 5px;
  position: absolute;
  top: 5px;
  width: 10px;
}
