.icons {
  height: 15px;
  width: 15px;
}

.icons .overlap-group-wrapper {
  height: 15px;
}

.icons .overlap-group {
  height: 15px;
  position: relative;
  width: 15px;
}

.icons .div {
  height: 15px;
  left: 0;
  position: absolute;
  top: 0;
  width: 15px;
}

.icons .rectangle {
  border: 1px solid;
  border-color: var(--yellow);
  height: 10px;
  left: 0;
  position: absolute;
  top: 0;
  width: 10px;
}

.icons .rectangle-2 {
  border: 1px solid;
  border-color: var(--yellow);
  height: 10px;
  left: 5px;
  position: absolute;
  top: 5px;
  width: 10px;
}
