@import url("https://fonts.googleapis.com/css?family=Inter:var(--h6-16px-font-weight),400,var(--paragraph-progress-15-font-weight),var(--p-15px-paragraph-font-weight),var(--h4-18px-font-weight),600,var(--h2-32px-font-weight),500,700|Roboto:700");
@import url("https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css");
      * {
        -webkit-font-smoothing: antialiased;
        box-sizing: border-box;
      }
      html,
      body {
        margin: 0px;
        height: 100%;
      }
      /* a blue color as a generic focus style */
      button:focus-visible {
        outline: 2px solid #4a90e2 !important;
        outline: -webkit-focus-ring-color auto 5px !important;
      }
      a {
        text-decoration: none;
      }